
html{
    margin: 0px;
    padding: 0px;
    display: block;
}
body{
    font: 14px 'Helvetica Neue', Helvetica, 
                Arial, sans-serif;
    line-height: 1.4em;
    background: #f5f5f5;
    color: #4d4d4d;
    min-width: 230px;
    max-width: 1440px;
    margin: 0 auto;
    font-weight: 300px;
    -webkit-font-smoothing: antialiased;
}
.rootdiv{
    background: rgb(255, 255, 255);
    margin: 200px 0 40px 0;
    position: relative;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
                0 25px 50px rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
                0 25px 50px rgba(0, 0, 0, 0.1);
}
.rootdiv h1{
    position: absolute;
    top: -200px;
    width: 100%;
    font-size: 150px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15)
}
div{
    display: block;
}
header, section{
    display: block;
}
.todoinput{
    padding: 16px 16px 16px 60px;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    -webkit-box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
            box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
    position: relative;
    margin: 0;
    width: 100%;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: inherit;
    -webkit-font-smoothing: antialiased;
    font-style: italic;
}
.todolister{
    position: relative;
    z-index: 2;
    border-top: 1px solid #e6e6e6;
}
.todolister_input{
    position: absolute;
    top: -55px;
    left: -12px;
   
    height: 34px;
    
    
    -webkit-transform: rotate(90deg);
    
    
            transform: rotate(90deg);
    -webkit-appearance: none;
}
.todolister_input:before{
    content: '\276F';
    font-size: 22px;
    color: #e6e6e6;
    padding: 10px 27px 10px 27px;
}
.todolister_input:checked:before{
    color: #737373;
}
button, input[type="checkbox"]
{
    outline: none;
}
.unsortl_1{
    margin: 0;
    padding: 0;
    list-style: none;
    /* display: block; */
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 40px;
}
.unsortl_li{
    position: relative;
    font-size: 24px;
    border-bottom: 1px solid #ededed;
    display: list-item;
    text-align: -webkit-match-parent;
}
.unsortl_1 li .toggle {
    height: 40px;
    background: none;
    width: 40px;
    position: absolute;
    top: 12px;
    left: -52px;
    bottom: 0;
    margin: auto 0;
    border: none;
    -webkit-appearance: none;
    /* float: left; */
}
.unsortl_1 li.completed label{
    color: #d9d9d9;
    text-decoration: line-through;
}
.unsortl_1 li .toggle:checked::after{
    content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>');
}
.toggle:after {
    content:url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>');
}
.del_todo_bt{
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0px;
    width: 40px;
    height: 40px;
    margin: auto 0;
    font-size: 30px;
    color: #cc9a9a;
    margin-bottom: 11px;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
}
.del_todo_bt::after{
    content: "Ã—";
}
.del_todo_bt:hover {
	color: #af5b5e;
}
.unsortl_1 li:hover .del_todo_bt {
	display: block;
}
button{
     padding: 0;
    border: 0;
    background: none;
    vertical-align: baseline;
    -webkit-appearance: none;
    -webkit-font-smoothing: antialiased;
}
.unsortedl_label{
    word-break: break-all;
    padding: 15px 60px 15px 15px;
    margin-left: 20px;
    display: block;
    line-height: 1.2;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
    text-align: left;
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 24px;
}
.footer{
    color: #777;
    padding: 10px 15px;
    height: 20px;
    text-align: center;
    border-top: 1px solid #e6e6e6;
}
.footer:before{
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    height: 50px;
    overflow: hidden;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
.leftitem{
    float: left;
}
.filters{
    margin: 0;
    padding: 0;
    list-style: none;
    position: absolute;
    right: 0;
    left: 0;
}
.filters li a{
    color: inherit;
    margin: 3px;
    padding: 3px
}
.filters li a.selected{
    border-color: rgba(175,47,47, 0.2);
}
.view{
    position: relative;
}
.editer{
    display: none;
    position: absolute;
    margin: 0;
    left: 55px;
    top: 15px;
    width: 90%;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
}




